.indp{
    background-color: $white;

    .section{
        width: 100%;
        padding: 8px 0;

        .fund__container--btn{
            width: 100%;
            padding: 0;
        }

        &__container{
            margin-bottom: 40px;

            & h4.section__container--subtitle{
                @include heading-two;
                margin-bottom: 16px;
            }

            &--box{

                & h5.section__container--box--subtitle{
                    @include sub-heading-one;
                    margin-bottom: 6px;
                }
            }
        }
    }
    
    &__section{
        width: 100%;
        padding: 48px 80px;
        margin: 0 auto;
    }

    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 24px 0;
        width: 100%;

        &--textContent{
            h1{
                margin-bottom: 0;
                @include heading-one;
            }
            .note__box{
                padding: 10px 0;
            }
        }

        &--logo{
            width: 208px;
            // height: 84px;
            cursor: pointer;
            align-self: flex-start;

            & img{
                display: block;
                max-width: 100%;
            }
        }
    }
    
}