.imageUploader{
    width: 746px;
    height: 258px;
    border-radius: 9px;
    border: 1px solid $stroke-secondary;
    display: flex;
    align-items: center;
    justify-content: center;

    &__container{
        border: 2px dashed $color-primary;
        width: 708px;
        height: 220px;
        border-radius: 9px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &--input{
            width: 172px;
            height: 136px;
            display: block;
            opacity: 0;
            z-index: 10;
            cursor: pointer;

            &.img-opacity{
                opacity: 1;
                width: auto;
                height: auto;
                align-self: center;
            }
        }

        .uploaded{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            min-width: 100%;

            &__container{
                display: flex;
                flex-direction: row;
                // justify-content: flex-start;
                justify-content: space-between;
                align-items: center;

                & > span:first-child{
                    word-break: break-all;
                    min-width: 75%;
                    height: inherit;
                }

                span:last-child{
                    min-width: 10%;
                }

                .spinner-border-sm{
                    width: 14px;
                    height: 14px;
                    vertical-align: middle;
                    border-width: 0.1em;
                    color: $color-primary;
                }

                svg{
                    // margin-left: 7px;
                    color: $color-success;
                    font-size: 14px;
                }
    
                svg[data-icon="times"]{
                    vertical-align: middle;
                    color: $color-incorrect;
                }

            }
        }
        
        #imageUploader__container--p{
            font-size: 12px;
            color: $color-primary;
            font-weight: 500;
            text-align: center;
            position: absolute;
            width: 172px;
            display: block;
            z-index: 1;
            margin-bottom: 0;

            &::before{
                content: '';
                position: absolute;
                top: 20%;
                left: 25%;
                width: 32px;
                height: 32px;
                background: url('../../includes/add-folder-icon.svg');
                background-repeat: no-repeat;
                transform: translate(-50%, -50%);
            }
        }
    }
}