.section{
    label{
        // color: $text-color-label;
        cursor: pointer;
        // font-weight: 600;
        // font-size: 16px;
        @include label;
    
        span{
            color: $color-primary;
        }
    }
}

.form-input-radio{ //div that contains input radio and label

    padding: 5px 0;

    label{
        @include label;
        cursor: pointer;
        margin-bottom: 0;
    }

    input[type="radio"]{
        vertical-align: middle;
        margin-right: 8px;
        accent-color: $color-primary;
        cursor: pointer;
    }
}

.form-input-radio.row-input{
    display: flex;
    align-items: flex-start;

    input[type="radio"]{
        margin-top: 10px;
    }
}

//Custom Input - section A : Firm Commitment Amount
.input__holder{
    position: relative;
    border: 1px solid $stroke-primary;
    width: 427px;
    height: 52px;
    padding: 7px;
    border-radius: 8px;
    display: flex;
    align-items: center;

    .input__holder--icon{
        background-color: #fff;
        margin: 0 10px;
        font-size: 16px;
        color: $stroke-primary;
    }

    .custom-input{
        width: 100%;
        border: none;
    }

    &.incorrect{
        border: 1px solid $color-incorrect;
    }
}

//section E
input[type="radio"] + label + input[type="text"]{
    margin-left: 10px;
}

.input__desc{//section A Firm Commitment Amount

    width: 60%;

    &.full-width{
        width: 100%;
    }

    &--p{
        padding: 10px 0;

        &.u-padding-0{
            padding: 0;
        }

        @include body-two;

        margin-bottom: 0;
    }
}

//common input text field
.sub__form{
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;

    .form-input{
        max-width: 427px;

        @include respond(big-desktop){
            max-width: 40%;
        }
    }

    // .form-input:nth-child(odd){
    //     margin-right: 72px;
    // }
    .form-input:not(:last-child){
        margin-right: 72px;
    }

    .input__desc{
        width: fit-content;

        &--p{
            margin: 0;
        }
    }
}

.form-input{

    display: flex;
    flex-direction: column;
    margin-bottom: 30px;

    &.form-input-full-width{
        width: 926px;
    }

    &.u-margin-bottom-0{
        margin-bottom: 0;
    }

    label{
        @include label;
    }
}
.form-input-common{
    padding: 5px 10px;
    border: 1px solid $stroke-primary;
    border-radius: 8px;
    width: 427px;
    height: 52px;
    max-width: 926px;
    font-size: 14px;
}

.form-input.input-co-button{ //custom section b primary email field
    flex-direction: row;
    flex-wrap: wrap;
	flex-grow: 2;

	.form-input-common{
		max-width: 356px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-right: none;
	}

    .input-co-btn{
        width: 100px;
        border: none;
        color: $white;
        background-color: $color-primary;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        font-size: 14px;
        font-weight: 700;
    }
}

.form-input-full-width.input-co-button{ //custom section b primary email field
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
	flex-grow: 2;
    max-width: 926px;

    &.dark{

        .input-co-btn{
            background-color: $text-color-primary;
        }
    }

	.form-input-common{
        max-width: 85%;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-right: none;
	}

    .input-co-btn{
        width: 15%;
        border: none;
        color: $white;
        background-color: $color-primary;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        font-size: 14px;
        font-weight: 700;
    }
}

.form-input-full-width{
     
    input.form-input-common{
        width: 100%;
        max-width: 926px;
    }

    .input__desc{
        width: 100%;

        &--p{
            margin-bottom: 0;
        }
    }
}
.input-checkbox{
    display: flex;
    align-items: center;

    &.text-default-size label{
        font-size: 16px;
    }

    input[type="checkbox"]{
        width: 16px;
        height: 16px;
        accent-color: $color-primary;
        cursor: pointer;
    }

    label{
        margin-bottom: 0;
        margin-left: 7px;
        // font-weight: 500;
        // font-size: 14px;
        // color: $stroke-primary;
        cursor: pointer;
        @include label;
    }

    &.dark{
        
        label{
            color: $text-color-tertiary;
            font-size: 16px;
            line-height: 32px;
        }
    }
}

/*overwrite bootstrap form select styles*/
#reason0,
#reason1,
#reason2,
#reason3,
#reason00,
#reason10,
#reason01,
#reason11,
#reason02,
#reason12,
#sec-g-con-1-reason,
#sec-g-1-professional-body{
    &.form-control{
        border: 1px solid $stroke-primary;
        padding: 16px 10px;
        height: 52px;
        cursor: pointer;
        
        &:focus{
            box-shadow: none;
            border: 1px solid $stroke-primary;
        }
    }
}

.radio-inputs-row{
    display: flex;
    width: 80%;

    & div:first-child{ 
        margin-right: 20px;
    }
}

.form-input.incorrect{

    .form-input-common{

        border: 1px solid $color-incorrect;

    }

}