.custom__container{
    margin-bottom: 24px;
    
    &.u-margin-bottom-lg{
        margin-bottom: 16px;
    }

    .custom__input{
        width: 419px;
        height: 56px;
        border: 1px solid $stroke-primary;
        border-radius: 8px;
        padding: 16px;
        display: flex;
        align-items: center;

        &.incorrect{
            border: 1px solid $color-incorrect;
        }
    
        &.dark{
            border: 1px solid $text-color-primary;
            color: $text-color-primary;
    
            .custom__input--icon{
                color: $text-color-primary;
                margin-right: 16px;
            }
        }
    
        &--icon{
            color: $stroke-primary;
            margin-right: 16px;
            padding: 0 8px;
            cursor: pointer;
            width: fit-content;

            svg{
                width: 13px;
                height: 13px;
            }
        }
    
        & input{
            border: none;
            width: 100%;
            height: 100%;
            font-size: 14px;
            font-weight: 500;
        }

        // input[type="password"]{
        //     height: 80%;
        //     font-size: 64px;
        //     align-self: baseline;
        // }
    }
    p{
        font-size: 14px;
        color: $color-incorrect;
    }
}

div.custom__input:last-child{
    margin-bottom: 0px;
}