body{
    background: $white;
}
h5{
    span{
        color: $color-primary;
    }
}
h6{
    span{
        color: $color-primary;
        margin-left: 5px;
    }
}
.row-of-inputs{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
}
.flex-container{
    display: flex;
    flex-wrap: wrap;

    .input-checkbox{
        margin-right: 20px;
    }
}
.input__desc--p.incorrect{
    color: $color-incorrect;
    font-weight: 500;
}
.col-layout{
    max-width: 926px;

    .country__dropdown{
        width: 926px;
    }

    .dropdown__main{
        max-width: 926px;
    }

    .input-co-button label{
        display: block;
        width: 500px;
    }
}
#approval-form{
    padding-bottom: 0;
}
.a-links{
    font-weight: 500;
    font-size: 16px;
}
#after-req{
    font-size: 14px;
    text-align: center;
    color: $text-color-primary;
    padding: 20px;
    display: flex;
    height: 200px;
    border: 1px dashed #ee9401;
    border-radius: 5px;
    margin: 30px auto;
    align-items: center;
    justify-content: center;
    background-color: $color-message-bg;
}
#form__image .customShimmer{
    animation-duration: 8s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: shimmer;
    animation-timing-function: linear;
    background: #f8eeef;
    // background: linear-gradient(to right, #f6f6f6 8%, #f0f0f0 18%, #f6f6f6 33%);
    background-size: 1200px 100%;
    background: linear-gradient(to right, #f6f6f6 8%, #f0f0f0 18%, #f6f6f6 33%);
    min-width: 40%;
    min-height: 300px;
}
.note__box.bold p{
    @include body-two;
    color: $text-color-primary;
}
.row-space-between{
    display: flex;
    max-width: 926px;

    & .form-input-radio:not(:last-child){
        margin-right: 64px;
    }
}
.select-dropdown-width{
    max-width: 926px;

    & #sec-g-1-professional-body.incorrect-select{
        border: 1px solid $color-incorrect;
    }
}
.row-sm{
    width: 50%;
}
#cancelBtn-standard{
    background-color: $white;
    color: $color-primary;
    box-shadow: none;
    border: 1px solid $color-primary;

    &:hover,
    &:focus{
        box-shadow: none;
    }
}
.control-dropdown-height{
    max-height: 230px;
    overflow-y: scroll;
}
.info-icon{
    width: 14px;
    height: 14px;
}