.dropdown-global{
    max-width: 926px;
}
.country__dropdown{
    width: 427px;
    margin-right: 72px;
    // margin-bottom: 30px;
    // max-height: 52px;

    #select-dropdown-country{
        height: 52px;
    
        .css-1vl30tz-control{
            max-width: 427px;
        }

        .css-13cymwt-control, .css-t3ipsp-control{
            height: 52px;
        }

        .css-13cymwt-control{
            border-color: $stroke-primary;
            border-radius: 8px;
        }

        .css-t3ipsp-control{
            border: 1px solid $stroke-primary;
            box-shadow: none;
            border-radius: 8px;
        }

        .css-1u9des2-indicatorSeparator{
            width: 0;
            background-color: $white;
        }
        .css-tj5bde-Svg{
            fill: #212121;
        }
    }

    #select-dropdown-country.incorrect{
        .css-13cymwt-control{
            border-color: $color-incorrect;
        }

        .css-t3ipsp-control{
            border: 1px solid $color-incorrect;
        }
    }
}