.signature__wrapper{
    width: 456px;
    height: 180px;
    border-radius: 9px;
    border: 1px solid $stroke-secondary;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &--container{
        border: 1px dashed $color-primary;
        width: 418px;
        height: 142px;
        border-radius: 9px;
        position: relative;

        canvas{
            width: 100%;
            height: 100%;
        }

        &--btn{
            position: absolute;
            right: 2%;
            bottom: 5%;
            
            button{
                border: none;
                background-color: transparent;
                color: $color-primary;
                text-decoration: none;

                &:hover{
                    text-decoration: underline;
                }

                svg[data-icon="check"]{
                    color: $color-success;
                }
            }

        }
    }
}