#section-wrapper{

    .two-col{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    
        .fund__info{
            width: 50%;
    
            .fund__container{ 
                width: 100%;
            }
    
            h5#fund__info--title{
                // @include sub-heading-one;
                @include heading-two;
            }
    
            h6.fund__container--type--h6{
                @include sub-heading-one;
            }
    
            #fund-dynamic {
                body p{
                    background-color: $white;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 28px;
                    color: $text-color-tertiary;
                }
            }
        }
    
        .fund__logo{
            width: 40%;
            img{
                display: block;
                max-width: 100%;
                height: fit-content;
            }
        }
    }

    #terms-container{
            
        body{
            // width: 100% !important; //to overwrite third party styles - [ Using !important NOT A RECOMMENDED ]
            // height: 300px !important; 
            background-color: $white !important;
            padding: 20px 0;
        }
    }
    
    .terms{
    
        &__h6{
            @include heading-two;
        }
    
        #agreement-to-fund-terms-and-conditions + label{
            @include sub-heading-two;
            padding: 10px;
        }

        #scrollable-field{
            min-width: 80%;
            width: 100% !important; //to overwrite third party styles - [ Using !important NOT A RECOMMENDED ]
            height: 300px !important; 
            border: 1px solid $stroke-primary !important;
            background-color: $white !important;
            border-radius: 8px;
        }
    
    }
}