#demoForm-wrapper{
    max-width: 100%;
    min-height: 100vh;
    height: 100%;
    padding-bottom: 40px;
    background-color: $background-primary;
    font-family: 'Montserrat', sans-serif;

    .demoForm__header{
        width: 100%;
        margin: 0 auto;
        padding: 24px 0;
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: sticky;
        top: 0;
        z-index: 999;
        background-color: $white;
        box-shadow: 5px -6px 10px 3px rgba(0, 0, 0, .2);
    }
    
