    .demoForm__header--progressBar{
        width: 50%;

        @include respond(big-desktop){
            width: 52%;
        }

        & .progress, & .progress-bar:nth-last-child(1){
            border-radius: 5rem;
        }

        & .progress{
            background-color: $background-tertiary;
        }
        & .progress-bar:nth-last-child(1){
            background-color: $color-primary;
            opacity: 9;
        }
    }

    .demoForm__header--prevButton{

        .fa-icons{
            margin-right: 8px;
        }

        & .btn-secondary{
            color: $color-primary;
            background-color: $white;
            border-color: $color-primary;
        }

        & .btn-secondary:hover{
            background: $color-primary;
            color: $white;
        }

        & .btn-secondary:hover .fa-icons{
            color: $white;
        }

        .disabled{
            opacity: .3;
        }

    }
    
}