.btn-demo{
    width: 206px;
    height: 60px;
    border: .5px solid $color-primary;
    font-size: 18px;
    border-radius: $btn-border-radius;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
.btn-primary-forms{
    background-color: $color-primary;
    color: $white;
}
.btn-secondary{
    border: 1px solid $color-primary;
    background-color: $background-primary;
    color: $color-primary;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}
.btn-secondary:hover,
.btn-secondary:active,
.btn-secondary:not(:disabled):not(.disabled).active, .btn-secondary:not(:disabled):not(.disabled):active,
.show>.btn-secondary.dropdown-toggle,
.btn-secondary:not(:disabled):not(.disabled):active:focus,
.btn-secondary:focus{
    border: 1px solid $color-primary;
    background: $color-primary;
    color: $white;
    box-shadow: none;
}
.btn-secondary.disabled, .btn-secondary:disabled{
    color: $color-primary;
    background: $white;
    border: 1px solid $color-primary;
    opacity: .6;
    pointer-events: none;
}
.btn-primary-xl{
    width: 280px;
    height: 60px;
}
.btn-demo.btn-primary-forms:hover{
    color: $color-primary;
    background-color: $white;
}
.btn-demo.btn-secondary:hover .fa-icons{
    color: $white;
}
.fa-arrow-left{
    margin-right: 8px;
    font-size: 12px;
}
.fa-icons{
    font-size: 12px;
    color: $color-primary;
}
#download-pdf .fa-file-pdf{
    margin-left: 8px;
}
.disabled{
    opacity: .6;
}
.link-button{
    border: none;
    color: $color-primary;
    font-weight: 500;
    font-size: 20px;
    background-color: transparent;
    display: block;
    margin: 0 auto;
    
    &:hover{
        text-decoration: underline;
    }
}

// external forms
.forms-btn{
    width: 293px;
    height: 64px;
    border-radius: 8px;
}
#dashboard-btn{
    background-color: $color-primary;
    color: $white;
    
    &:hover{
        text-decoration: none;
    }
}
.dashboard-btn{
    padding: 32px 24px;
    border: none;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    color: $stroke-primary;
    font-size: 14px;
    background: $white;
    border-bottom: 1px solid $stroke-secondary;

    &:hover{
        text-decoration: underline;
    }

    .icon{
        width: 32px;
        display: inline-block;
        margin-right: 13px;
        
        img{
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
}