.application{

    & * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    background-color: $white;
    min-height: 100vh;
    // padding: 20px;
    padding: 40px;
    font-family: 'Montserrat', sans-serif;

    &__header{
        // padding: 10px 0;
        background: $white;

        @include respond(big-desktop){
            padding: 20px;
        }

        a{
            display: block;
            cursor: pointer;
            width: 208px;
        }

        &--logo{
            width: 214px;
            height: 52px;
            
            img{
                display: block;
                max-width: 100%;
            }
        }
    }

    .messageText{
        font-size: 14px;
        font-weight: 500;
        font-family: 'Montserrat', sans-serif;
        padding: 5px;

        & svg{
            width: 13px;
            height: 13px;
            margin-right: 3px;
        }

        &.success{
            color: $color-success;
        }

        &.error{
            color: $color-incorrect;
        }
    }

    &__container{
        // width: 80%;
        margin: 0 auto;
        padding: 0 20px;
        width: 418px;

        @include respond(big-desktop){
            padding: 20px;
        }

        &--title{
            font-size: 32px;
            font-weight: 700;
            padding: 0 20px;
            margin: auto;
            color: $color-primary;

            @include respond(big-desktop){
                padding: 20px;
            }
        }
    }

    &__form{
        padding: 20px;
        background-color: $white;
        margin: 0 auto;
        margin-bottom: 48px;
        width: fit-content;
        min-width: 438px;

        @include respond(big-desktop){
            padding: 50px;
        }

        &--title{
            font-size: 18px;
            font-weight: 600;
            padding: 10px 0;

            & + .text__box .text__box--p{
                @include sub-heading-three;
            }
        }

        &--subtitle{
            background-color: #EDFDEC;
            font-size: 14px;
            color: $color-success;
            padding: 10px 5px;
            border: 1px solid $color-success;
            border-radius: 8px;
            display: inline-block;
            font-weight: 500;
            transition: all 2s ease-in-out;
            margin-bottom: 16px;
        }

        &--group{
            padding: 40px 0;
            padding-bottom: 0;
            width: fit-content;
            // margin: 0 auto;
            background-color: $white;
        }

        &--link{
            color: $color-primary;
            padding: 20px 0;
            font-size: 16px;
            font-weight: 600;
            display: flex;
            align-items: center;
            flex-direction: column;

            & div{
                width: 100%;
            }

            &--secondary{
                display: flex;
                margin-bottom: 10px;
                justify-content: flex-end;
            }

            button{
                width: 418px;
                height: 56px;
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 42px;

                & + a{
                    font-size: 16px;
                    &.u-padding-sm-y{
                        padding: 5px 0;
                    }
                }
            }
        }
    }
}