.review{
    position: relative;

    .section-wrapper{

        .demoForm__header{
            
            &--progressBar{
                visibility: hidden;
            }
        }
    }
}

// PDF
.review-section{
    background: $white;
    margin: 0 auto;
    // padding: 40px 0;

    h2{
        font-size: 36px;
        font-weight: 700;
        color: $color-primary;
        padding: 0 14px;
        margin-bottom: 0;
        display: flex;
        justify-content: space-between;
    }

    #form-review{
        width: 100%;

        body{
            background-color: $white;
            margin: 0;
            padding: 0;
            width: 100%;
            font-family: 'Montserrat', sans-serif !important;

            .review-sub{
                width: 100%;

                .fund{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    padding: 0 16px;
                    padding-bottom: 20px;

                    &__container{
                        width: 50%;

                        .fundContainer{
                        
                            h2{
                                padding: 0;
                                margin-bottom: .5rem;
                            }
    
                            &__type{
                                // padding: 20px 0;
                                
                                & p{
                                    line-height: 28px;
                                    color: $text-color-tertiary;
                                    font-size: 16px;
                                    font-weight: 500;
                                }
                            }

                            h2.fundContainer__title{
                                @include sub-heading-one;
                            }
                        }
                    }

                    & + .fundContainer{
                        padding-bottom: 30px;

                        .fundContainer__type{
                            padding: 0 16px;

                            &--p{
                                line-height: 32px;
                                color: $text-color-tertiary;
                                font-size: 16px;
                                font-weight: 500;
                            }
                        }

                        & + .customTable{

                            padding: 0 16px;
                        }
                    }

                    &__title{
                        padding: 16px 0;
                        @include heading-two;
                    }

                    
                }
            }

        }
    }
    .review-btn{
        padding: 0 16px;
    }

    .customTable{

        padding: 20px 0;

        &__header{
            background-color: $background-tertiary;
            border: 1px solid $border-color-primary;
            padding: 16px 25px;
            border-bottom: 0px;
            font-weight: 600;
            font-size: 16px;
            color: $color-primary;

            h6{
                margin-bottom: 0;
            }
        }

        &__body{

            border: 1px solid $border-color-primary;
            margin-bottom: 30px;

            &--row{

                padding: 12px 8px 0px 8px;

                &:last-child{
                    padding-bottom: 12px;
                }

                h6, p{
                    padding: 4px 17px;
                    margin-bottom: 0;
                }

                p{
                    font-size: 14px;
                    color: $text-color-tertiary;
                    font-weight: 500;
                    line-height: 32px;
                }

                .image__container{
                    margin: 8px 17px;
                    width: 456px;
                    height: 180px;
                    background-color: $primary-light;
                    overflow: hidden;

                    img{
                        display: block;
                        max-width: 100%;
                        height: auto;
                    }
                }

            }

            // &--row:nth-child(odd){
            //     background-color: $primary-light;
            // }
        }
    }
}

#download-pdf{

    .modal-header{
        background: $color-primary;
        color: $white;
    }
    
    .modal-body{
        h6.success-message{
            color: $color-success;
            svg{
                font-size: 10px;
                margin: 0 5px;
            }
        }
        em h5{
            color: $text-color-primary;
        }
        h6.download-text svg{
            font-size: 10px;
            margin: 0 5px;
        }
    }
    
    .modal-body h6{
        color: $text-color-primary;
        font-weight: 600;
    }

    .close:not(:disabled):not(.disabled):focus,
    .close:not(:disabled):not(.disabled):hover {
        opacity: 1;
    }

    .close:hover {
        color: #fff;
        text-decoration: none;
    }
}