.divider-or{
    padding: 16px 0;
    color: $color-primary;
    position: relative;

    &::before{
        content: '';
        position: absolute;
        height: 1.5px;
        background-color: $color-primary;
        width: 100%;
    }

    &::after{
        content:'or';
        color: $color-primary;
        font-weight: 700;
        font-size: 18px;
        padding: 7px 15px;
        position: absolute;
        top: 50%;
        left: 50%;
        background-color: $white;
        transform: translate(-50%, -50%);
    }
}
