.docPlaceholder{
    border: 1px solid rgba(144, 144, 144, .3); //909090
    border-radius: 9px;
    margin-bottom: 10px;
    width: 746px;
    height: 258px;
    padding: 19px;


    &__container{
        border: 2px dashed $color-primary; //AC162C
        height: 100%;
        border-radius: 9px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &--input{
            width: 172px;
            height: 136px;
            display: block;
            opacity: 0;
            z-index: 1;
            cursor: pointer;

            &.doc-opacity{
                opacity: 1;
                width: auto;
                height: auto;
                align-self: center;
            }
        }

        #uploadDocLabel{
            font-weight: 500;
            font-size: 12px;
            color: $color-primary;
            margin-bottom: 26px;
            line-height: 20px;
        }

        &--p{
            font-size: 12px;
            color: $color-primary;
            font-weight: 500;
            text-align: center;
            position: absolute;
            width: 172px;
            display: block;
            z-index: 1;

            &::after{
                content: 'Browse file';
                position: absolute;
                top: 103%;
                left: 50%;
                width: 160px;
                height: 40px;
                padding: 11px;
                border-radius: 6px;
                color: $white;
                background: $color-primary;
                transform: translate(-50%, 60%);
            }

            &::before{
                content: '';
                position: absolute;
                top: -50%;
                left: 50%;
                width: 32px;
                height: 32px;
                background: url('../../includes/add-folder-icon.svg');
                background-repeat: no-repeat;
                transform: translate(-50%, -50%);
            }
        }
    }

    &__uploaded{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 10px 0;

        &--responses{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px 0;

            .link-button{
                padding: 1px 0;
                cursor: pointer;

                &:hover{
                    color: $color-primary;
                }
            }

            .link-button,
            button{
                font-size: 14px;
            }
            
            .docPreviewDelIcon{
                color: $color-primary;
                cursor:  pointer;
            }

            .uploaded-file-label{
                font-size: 14px;
                color: #000;
                margin-left: 5px;
            }

            .spinner-border-sm{
                width: 14px;
                height: 14px;
                vertical-align: middle;
                border-width: 0.1em;
                color: $color-primary;
            }

            svg{
                color: $color-success;
                font-size: 14px;
            }

            svg[data-icon="times"]{
                vertical-align: middle;
                color: $color-incorrect;
            }

            &:not(:last-child){
                border-bottom: 1px dashed $stroke-secondary;
            }
        }
    }
}