.dropdown__main{
    position: relative;

    &--selected{
        border: 1px solid $stroke-primary;
        border-radius: 8px;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;

        label{
            margin-bottom: 0;
            font-weight: 400;
        }
    }

    &--options{
        margin-top: 10px;
        padding: 10px;
        border-radius: 8px;
        border: 1px solid $stroke-secondary;
        position: absolute;
        background-color: #fff; 
        z-index: 12;
        right: 0px;
        left: 0px;
        box-shadow: 0px 1px 2px 0px rgba(60, 64, 67, 0.3), 0px 1px 3px 1px rgba(60, 64, 67, 0.15);
        p{
            padding: 10px;
            cursor: pointer;
            border-radius: 8px;
            transition: all .2s ease-in-out;
            margin-bottom: 0;

            &:hover{
                background-color: $color-primary;
                color: $white;
            }
        }
    }
}