@mixin heading-one{
    font-size: 32px;
    font-weight: 700; //BOLD
    line-height: 39px;
    color: $color-primary; //AC162C
}
@mixin heading-two{
    font-size: 18px;
    font-weight: 600; //SEMI BOLD
    color: $color-primary; //AC162C
    line-height: 22px;
}
@mixin sub-heading-one{
    font-size: 16px;
    font-weight: 700; //BOLD
    line-height: 20px;
    color: $text-color-primary; //212121
}
@mixin sub-heading-two{
    font-size: 16px;
    font-weight: 600; //SEMI BOLD
    color: $text-color-label; //424242
    line-height: 20px;
}
@mixin sub-heading-three{
    font-size: 16px;
    font-weight: 500; //MEDIUM
    color: $text-color-label; //424242
    line-height: 20px;
}
@mixin label{
    font-size: 16px;
    font-weight: 500; //MEDIUM
    color: $text-color-tertiary; //424242
    line-height: 24px;
}
@mixin body-one{
    font-size: 16px;
    font-weight: 600; //SEMI BOLD
    line-height: 22px;
    color: $text-color-primary; //212121
}
@mixin body-two{
    font-size: 14px;
    font-weight: 600; //SEMI BOLD
    color: $text-color-primary; //212121
    line-height: 24px;
}
@mixin notes{
    font-size: 14px;
    font-weight: 500; //SEMI BOLD
    color: $text-color-label; //424242
    line-height: 24px;
}
@mixin body-three{
    font-size: 14px;
    font-weight: 400; //REGULAR
    line-height: 26px;
    color: $text-color-tertiary; //616161
}
//Media query manager

/*
0-600px;        phone
600-900px;      Tablet portrait
900 - 1200px;    Tablet landscape
[1200 - 1800] is where our normal styles apply but not doing it until the design in silc.co is fixed
1800p + ;       big desktop 
$breakpoint argument choices:
- phone
- tab-port
- tab-land
- big-desktop
1em = 16px;
*/

@mixin respond($breakpoint){

    @if $breakpoint == phone{
        @media only screen and (max-width: 37.5em){ @content; }     //600px/16px = 37.5em
    }

    @if $breakpoint == tab-port{
        @media only screen and (max-width: 56.25em){ @content; }    //900px/16px = 56.25em
    }

    @if $breakpoint == tab-land{
        @media only screen and (max-width: 75em){ @content; }        //1200px/16px = 75em
    }

    @if $breakpoint == big-desktop{
        @media only screen and (min-width: 112.5em){ @content; }    //1800px/16px = 112.5em
    }
}