.appForm{
    font-family: 'Montserrat', sans-serif;

    .secondary__text{
        font-size: 16px;
        font-weight: 500;
        color: $stroke-primary;
        margin-bottom: 0;
    }

    .userDashboard{
        min-height: 100vh;

        .header{
            width: 100%;
            background-color: $white;
            padding: 20px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid $stroke-secondary;
            height: 20.5vh;

            &__wrapper{
                width: 95%;
                margin: 0 auto;
            }

            &__container{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 95%;
                margin: 0 40px;
                padding: 20px 0;

                &--title{
    
                    h1{
                        font-size: 32px;
                        font-weight: 700;
                        color: $text-color-primary;
                        text-transform: capitalize;
                    }
                }

                &--right{

                    display: flex;
                    align-items: center;

                    &--initials{
                        border: 1px solid $color-primary;
                        width: 56px;
                        height: 56px;
                        border-radius: 8px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-right: 30px;
    
                        div{
                            // width: 24px;
                            height: 24px;
                            text-align: center;
                            text-transform: uppercase;
    
                            p{
                                color: $color-primary;
                                // font-size: 16px;
                                font-weight: 600;
                            }
                        }
                    }

                    &--icon{
                        cursor: pointer;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        button{
                            width: 56px;
                            height: 56px;
                            border: 1px solid $color-primary;
                            background-color: $white;
                            color: $color-primary;
                            border-radius: 8px;

                            &:hover{
                                background-color: $color-primary;
                                color: $white;
                            }
                        }
                    }
                }
            }
        }

        .mainDashboard{
            width: 95%;
            margin: 0 auto;
            margin-top: 40px;
            background-color: $white;
            border-radius: 8px;
            min-height: 74vh;

            &__container{
                width: 95%;
                margin: 0 40px;
                padding: 25px 0;
                min-height: 74vh;

                &--title{
                    font-weight: 700;
                    color: $text-color-primary;
                }

                &--box{
                    width: 100%;

                    &--flex{
                        padding: 20px 0;
                    }
                }

                &--sub{
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                }
            }

            &__form{
                padding: 20px 0;
                display: flex;
                flex-wrap: wrap;

                a{
                    display: block;
                    cursor: pointer;
                    text-decoration: none;
                }

                .box{
                    width: 231px;
                    height: 254px;
                    border: 1px solid $stroke-secondary;
                    border-radius: 6px;
                    padding: 20px 10px;
                    display: flex;
                    align-items: flex-end;
                    justify-content: center;
                    flex-wrap: wrap;
                    margin-right: 30px;
                    margin-bottom: 30px;

                    &__icon, &__form{
                        text-align: center;
                        font-size: 30px;
                        height: 80%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: space-between;

                        &:first-child{
                            color: $color-primary;
                        }

                        h4{
                            font-size: 16px;
                            font-weight: 600;
                            color: $text-color-primary;
                        }

                        button{
                            width: 195px;
                            height: 48px;
                            font-size: 14px;
                        }
                    }

                    &__form{

                        justify-content: flex-start;

                        h4{
                            padding-top: 30px;
                        }

                        p.secondary__text{
                            font-size: 12px;
                        }

                        &:not(:last-child){
                            margin-right: 30px;
                        }
                    }
                }
            }

            &__form{

                &--container{
                    width: 100%;

                    .forms__table{

                        #table{
                            background: $white;
                            width: 100%;
                            border-collapse: collapse;
                            
                            thead, tr{
                                background-color: $white;
                            }
                    
                            & thead{
                                font-weight: 600;
                                background: $white;
                                tr{
                                    border-top: .5pt solid #d8d8d8;
                                    border-bottom: .5pt solid #d8d8d8;
                                }
                                td{
                                    padding: 20px 15px;
                                }
                                td:nth-of-type(2){
                                    text-align: center;
                                }
                            }
                            
                            & tbody{
                                
                                tr{
                                    &:not(:last-child){
                                    
                                        border-bottom: .5pt solid #d8d8d8;
                                    }
                                    .link-button{
                                        font-size: 16px;
                                        margin: 0 auto;
                                        padding: 0;
                                    }
                                } 
                            }
                            
                            td{
                                padding: 10px 15px;
                                text-align: center;

                                p{ 
                                    margin: none;
                                }
                                
                                &#status{
                                    text-align: center;
                                }

                                &#status p{
                                    display: block;
                                    width: 223px;
                                    padding: 7.5px 10px; 
                                    border-radius: 6px;
                                    margin: auto;
                                    font-size: 14px;
                                    line-height: 17px;
                                    font-weight: 500;

                                    &.white{
                                        background-color: $white;
                                        color: #000000;
                                    }

                                    &.yellow{
                                        background-color: #FBFDE8;
                                        color: #FFC043;
                                    }

                                    &.red{
                                        background-color: #ff0000;
                                        color: $white;
                                    }

                                    &.green{
                                        background-color: #EDFDEC;
                                        color: #3A824F;
                                    }

                                    &.blue{
                                        background-color: #ECF6FD;
                                        color: #5A88D1;
                                    }

                                    &.purple{
                                        background-color: #8c0a98;
                                        color: $white;
                                    }
                                }

                                .table-download-pdf{
                                    display: block;
                                    border: none;
                                    margin: auto;
                                    color: $white;
                                    background-color: $color-primary;
                                    padding: 8px;
                                    border: 1px solid $color-primary;
                                    border-radius: 8px;
                                    transition: all .5s ease-in-out;

                                    .fa-file-pdf{
                                        margin-left: 8px;
                                    }

                                    &:hover{
                                        color: $color-primary;
                                    background: $white;
                                    }
                                }
                            }
                            
                            td:not(:last-child){
                                border-right: .5pt solid #d8d8d8;
                            }
                        }
                             
                    }

                    .form__list{
                        padding: 16px 0;
                        display: flex;
                        width: 100%;

                        ul{
                            list-style: none;
                            margin-bottom: 0;
                            padding: 0;
                            width: 100%;

                            li{
                                display: flex;
                                width: 100%;
                                justify-content: space-between;
                                align-items: center;
                                padding: 16px 0;

                                &{
                                    border-bottom: 1px solid $stroke-secondary;
                                }
                                h6{
                                    font-weight: 500;
                                    margin-bottom: 0;
                                }

                                a.form__list--item{
                                    display: flex;
                                    justify-content: space-between;
                                    flex-wrap: wrap;
                                    width: 100%;
                                }
                            }
                        }

                        &--sub{
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            flex-wrap: wrap;
                            color: $text-color-tertiary;

                            p{
                                font-weight: 500;
                                margin: 0 7px;
                                font-size: 14px;
                                margin-bottom: 0;
                            }

                            svg{
                                vertical-align: middle;
                                margin-left: 10px;
                            }
                        }
                    }
                }
            }
        }
    }

    .form__btn{
                        
        button{
            border: none;
            padding: 16px 20px;
            background-color: $white;
            color: $color-primary;
            border: 1px solid $color-primary;
            // border: 1px solid $stroke-secondary;
            border-radius: 8px;
            font-weight: 500;
            margin-bottom: 16px;
            
            svg{
                margin-right: 16px;
            }
        
            &:hover{
                background-color: $color-primary;
                color: $white;

                & svg{
                    color: $white;
                }
            }
        }
    }

}