#section-wrapper{
    display: flex;
    justify-content: space-between;
    position: relative;
    background: $white;
    min-height: 100vh;

    .section-wrapper{

        &__sidebar{
            width: 273px;
            border-right: 1px solid $stroke-secondary;
            position: fixed;
            z-index: 1;
            top: 0;
            left: 0;
            height: 100vh;
            background: $white;

            &--top{
                height: fit-content;
                padding: 30.5px 33.5px;
                border-bottom: 1px solid $stroke-secondary;
            }
        }


        &__right{
            width: 80%;
            width: calc(100% - 273px);
            position: absolute;
            right: 0;
            background: $white;

            .demoForm__header{
                position: sticky;
                top: 0;
                background: $white;
                z-index: 3;
                padding: 30.5px 40px;
                border-bottom: 1px solid $stroke-secondary;

                &--progressBar{
                    height: 60px;
                    width: 90%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;

                    & .progress, & .progress-bar:nth-last-child(1){
                        border-radius: 5rem;
                    }
            
                    & .progress{
                        background-color: $background-tertiary;
                    }
                    & .progress-bar:nth-last-child(1){
                        background-color: $color-primary;
                        opacity: 9;
                    }
                }
            }

            .section__wrapper{
                width: 90%;
                padding: 40px;
                position: relative;

                .section{
                    min-height: 66vh;

                    &.height-100{
                        min-height: 100%;
                    }
                    
                    ul{
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 26px;
                        color: $text-color-label;
                    }
    
                    & h2.section__heading{
                        @include heading-one;
                        font-size: 31px;
                        margin-bottom: 16px;
                    }

                    .section__container{

                        margin-bottom: 40px;

                        &.u-margin-bottom-0{
                            margin-bottom: 0;
                        }

                        & h4.section__container--subtitle{
                            @include heading-two;
                            margin-bottom: 16px;
                        }

                        & h5{
                            @include sub-heading-one;
                        }

                        & h6{
                            @include sub-heading-two;
                        }

                        & .input__desc{
                            width: 100%;

                            & .input__desc--p{
                               color: $text-color-primary;
                               @include notes;

                               &.incorrect{
                                color: $color-incorrect;
                               }
                            }
                        }

                        & .text__box{
                            & .text__box--p{
                                @include body-three;
                            }
                        }

                        &--box{

                            // margin-bottom: 30px;
                            margin-bottom: 40px;

                            &--p{
                                @include body-three;
                            }

                            & h5.section__container--box--subtitle{
                                @include sub-heading-one;
                                margin-bottom: 6px;
                            }

                            & h6{
                                @include sub-heading-one;
                            }

                            & div h6{
                                @include sub-heading-one;
                            }

                            & .box__container{
                                padding: 10px 0;

                                &--row{
                                    display: flex;
                                    align-items: center;
                                    margin-bottom: 7px;

                                    h6{
                                        margin-bottom: 0;
                                        margin-right: 10px;
                                    }
                                }
                            }

                            & .box__container--subheading{
                                @include sub-heading-one;

                                .fund-name{
                                    color: $color-primary;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}