.section#fundCode{
    display: flex;
    align-items: center;

    h2#form__title{
        @include heading-one;
        margin-bottom: 0;
    }

    p.secondary__text{
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        color: #909090;
        margin-bottom: 0;
    }

    .form__code{
        padding-bottom: 0;
    }
    .custom__container{
        margin-bottom: 30px;
    }

    .section__container{
        width: 100%;
    }
}

.fund__container{
  width: 50%;  

    &--btn{
        width: 90%;
        padding: 0 40px;
        padding-bottom: 20px;

        @include respond(big-desktop){
            width: 51%;
        }
        
        // display: flex;
        // justify-content: space-between;
    
        & .btn-primary-forms {
    
            .fa-icons{
                margin-left: 8px;
                color: $white;
            }
    
            &:hover{
                background-color: $white;
                color: $color-primary;
                
                .fa-icons{
                    color: $color-primary;
                }
            }
        }
    }
}

.position_relative_container{
    position: relative;
}

.fund{
    width: 100%;
    margin: 0 auto;
    padding: 40px 0;

    & .two-col{
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    &__logo{
        width: 40%;
        border-radius: 9px;
        overflow: hidden;

        img{
            display: block;
            max-width: 100%;
            height: fit-content;
            border-radius: 9px;
        }
    }

    &__title{
        font-size: 32px;
        font-weight: 700;
        color: $color-primary;
        margin-bottom: 0;
    }

    & .fund__container{
        width: 100%;

        &.wrapper{
            width: 45%;
        }

        & .fund__container--type{
            padding: 16px 0;
            padding-bottom: 0;
    
            & .fund__container--type--h3{
                color: $text-color-primary;
                font-weight: 700;
                margin-bottom: 0;
                font-size: 22px;
            }
    
            & .fund__container--type--p{
                padding: 10px 0;
                padding-bottom: 0;
                line-height: 32px;
                color: $text-color-tertiary;
                font-size: 16px;
                letter-spacing: 2%;
                margin-bottom: 0;
                font-weight: 500;
            }

            & .investor{

                .investor__type{
                    padding: 16px 0;
                }
            }
            & .investor__note{
                line-height: 28px;
                letter-spacing: 2%;
                color: $text-color-tertiary;
                font-weight: 500;
                font-size: 16px;
            }

            & .trustee{

                padding: 10px 0;

                span{
                    color: $color-primary;
                }
            }

            .fund__container--type--h5{
                font-size: 16px;
                color: $text-color-primary;
                line-height: 32px;
                font-weight: 700;
            }
        }
    }
}
#fund-dynamic{

    body{
        background: $white;

        h1, h2, h3, h4, h5, h6{
            background: $white;
            padding: 16px 0;
        }

        p{
            background: $white;
            @include body-three;
            color: $text-color-tertiary;
        }
    }
    
}