@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

body{ 
    font-family: 'Montserrat', sans-serif;
}
.terms{
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    color: $text-color-tertiary;

    & * li, & * p{
        @include label;
    }
}

.text__box{

    &--p{
        @include body-two;
        margin-bottom: 0;
    }

    &--p.light-pro{
        color: $stroke-primary;
    }
}

.note__box{ //for all notes
    font-size: 16px;
    padding: 16px 0;

    span{
        font-weight: 700;
        color: $text-color-primary;
    }

    p{
        @include notes;
        margin-bottom: 0;
    }

    ul{
        @include notes;
        margin-bottom: 0;
    }

    p.u-margin-bottom{
        margin-bottom: 1em;
    }

    &.u-padding-0{
        padding: 0;
    }
}
.text-semi-bold{
    font-weight: 600;
    font-size: 16px;
}