.no-progress-page{
    
    .demoForm__header--progressBar{
        visibility: hidden;
    }
}
#form__title{
    font-size: 40px;
    color: $text-color-primary;
    text-align: center;
    margin-bottom: 16px;
}
.form__code{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 30px 0;
    margin: 0 auto;

    input[type="text"]{
        width: 66px;
        height: 74px;
        font-size: 40px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
        margin-bottom: 0;
        border-radius: 4px;
        border: 1px solid $stroke-primary;

        &:not(:last-child){
            margin-right: 21px;
        }
    }

    & + button{
        margin: 16px auto;
        display: block;
    }

    .custom__container p{
        text-align: center;
        padding: 10px 0;
        font-weight: 500;
    }
}

.link-button.small{
    font-size: 16px;
}