.backdrop{
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100vw;
    height: 100vh;
    transform-origin: 0 0;
    background-color: rgba(255, 255, 255, .6);
    z-index: 4;

    &__img{
        width: 100px;
        height: 100px;
        position: absolute;
        top: calc(50% - 50px);
        left: calc(50% - 50px);
        transform: translate(-50%, -50%);
        animation: .75s linear infinite animateSpinner;
        
        img{
            display: block;
            max-width: 100%;
        }
    }
}