.review__header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.review__header--title{
    width: 50%;
    display: inline-block;
    color: #AC162C;
}
.review__header--logo{
    width: 40%;
    max-width: 208px;
}
.review__header--logo img{
    max-width: 100%;
    display: block;
    overflow: hidden;
}
.customTable{
    padding: 20px 0;
}
.customTable__header{
    background-color: rgba(172, 22, 44, .06);
    border: 1px solid #d8d8d8;
    padding: 10px 25px;
    border-bottom: 0px;
    font-weight: 700;
    font-size: 22px;
    color: #ac162c;
}

.customTable__header h6{
    margin-bottom: 0;
    font-size: 16px;
}

/* .customTable__body--parent{
    border: 1px dashed #d8d8d8;
} */

.customTable__body{
    border: 1px solid #d8d8d8;
    width: 100%;
    margin: 10px 0;
    margin-bottom: 30px;
}

.customTable__body--row{
    padding: 12px 8px 0px 8px;
}

.customTable__body--row:last-child{
    padding-bottom: 12px;
}

.customTable__body--row h6{
    font-size: 18px;
    font-weight: 700;
}

.customTable__body--row h6, .customTable__body--row p{
    font-family: 'Montserrat';
    padding: 4px 17px;
    margin-bottom: 0;
}

.customTable__body--row p{
    font-size: 14px;
    color: #616161;
    font-weight: 400;
    line-height: 32px;
}

.customTable__body--row .image__container{
    margin: 8px 17px;
    width: 456px;
    height: 180px;
    background-color: #f5f5f5;
    overflow: hidden;
}

.customTable__body--row .image__container img{
    display: block;
    max-width: 100%;
    height: auto;
}
.btn-demo{
    width: 206px;
    height: 60px;
    border: .5px solid #ac162c;
    font-size: 18px;
    border-radius: 12px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
.btn-primary-forms{
    background-color: #ac162c;
    color: #fff;
}